body {
  font-family: helvetica, arial, sans-serif;
  background: #ccf;
  text-align: center;
}

nav a {
  display: inline-block;
  margin: 0 10px;
  padding: 5px;
  border-radius: 3px;
  text-decoration: none;
}

nav a.active,
nav a:hover {
  background: #bbe;
}

.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
}

h1:first-line {
  font-size: 50px;
}

img.main {
  padding: 30px;
  background: #fff;
  border-radius: 10px;
  -webkit-box-shadow: 3px 3px 3px rgba(0, 0, 0, .2);
          box-shadow: 3px 3px 3px rgba(0, 0, 0, .2);
  margin-bottom: 50px;
}

.dino:hover {
  -webkit-transition: background .1s, -webkit-transform .1s;
  transition: background .1s, -webkit-transform .1s;
  -o-transition: transform .1s, background .1s;
  transition: transform .1s, background .1s;
  transition: transform .1s, background .1s, -webkit-transform .1s;
  -webkit-transform: scale(1.3);
      -ms-transform: scale(1.3);
          transform: scale(1.3);
  background: #eee;
}

.dino:hover img {
  -webkit-animation-duration: .15s;
          animation-duration: .15s;
  -webkit-animation-name: bump;
          animation-name: bump;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-iteration-count: 2;
          animation-iteration-count: 2;
}

.dino {
  padding: 30px;
  width: 190px;
  height: 190px;
  background: #fff;
  border-radius: 10px;
  margin: 10px;
  -webkit-box-shadow: 3px 3px 3px rgba(0, 0, 0, .2);
          box-shadow: 3px 3px 3px rgba(0, 0, 0, .2)
}

.dino img {
  width: 130px;
  height: 130px;
}

.dino-data {
  list-style-type: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 0;
}

.dino-data li {
  padding: 5px 20px;
  margin: 2px 0;
  background: #bbe;
}

footer {
  width: 100%;
  bottom: 0;
  padding: 30px 0 70px 0;
}

footer a {
  text-decoration: none;
}

footer a:hover {
  text-decoration: underline;
}


@-webkit-keyframes bump {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    -webkit-transform: scale(1.8);
            transform: scale(1.8);
  }
}


@keyframes bump {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    -webkit-transform: scale(1.8);
            transform: scale(1.8);
  }
}


